/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-landscape() {
    .woocommerce-page .mkdf-content,
    div.woocommerce {
        .mkdf-quantity-buttons {
            $size: 57px;

            .mkdf-quantity-input {
                height: $size;
                line-height: $size;
                margin: 0 #{$size / 2} 0 0;
                width: $size;
            }

            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                height: #{$size / 2};
                line-height: #{$size / 2};
                width: #{$size / 2};
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */