/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */


@include ipad-landscape() {
    .mkdf-woocommerce-page {
        table.cart {
            td.actions {
                .coupon {
                    .input-text {
                        padding-top: 15px;
                        padding-bottom: 15px;
                    }
                }
            }
        }
    }
}

@include ipad-portrait() {
    .mkdf-woocommerce-page {
        table.cart {
            td.actions {
                .coupon {
                    .input-text {
                        width: 180px;
                        //margin: 0 17px 0 0;
                        //padding: 9px 15px;
                        //padding-top: 15px;
                        //padding-bottom: 15px;
                        margin-right: 17px;
                    }
                }
            }
        }
    }
}

@include phone-landscape() {
    .mkdf-woocommerce-page {
        table.cart {
            tr {
                th, td {
                    font-size: 12px;
                }

                &.cart_item {
                    td {
                        &.product-thumbnail {
                            width: auto;
                            padding-right: 0;

                            img {
                                display: none;
                            }
                        }
                    }
                }
            }

            td.actions {
                .coupon {
                    margin: 0 0 20px;

                    .input-text {
                        width: 100%;
                        margin: 0 10px 20px 0;
                    }
                }

                .coupon,
                > button {
                    float: none;
                    display: block;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */