/* ==========================================================================
   WooCommerce Buttons responsive styles - begin
   ========================================================================== */

@include ipad-landscape() {
    .woocommerce-page .mkdf-content,
    div.woocommerce {
        a.button,
        a.added_to_cart,
        input[type="submit"],
        button[type="submit"]:not(.mkdf-search-submit):not(.single_add_to_cart_button),
        .wc-forward:not(.added_to_cart):not(.checkout-button),
        .single_add_to_cart_button {
            @include mkdfButtonSize(large);
        }
    }
}

/* ==========================================================================
   WooCommerce Buttons responsive styles - end
   ========================================================================== */