/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@include laptop-landscape-large() {
    .mkdf-woo-single-page {
        .woocommerce-tabs {
            ul.tabs {
                > li {
                    a {
                        padding: 12px 31px;
                    }
                }
            }

            .woocommerce-Tabs-panel {
                padding: 30px 16% 0 16%;
            }
        }
    }
}

@include ipad-landscape() {
    .mkdf-woo-single-page {
        .mkdf-single-product-content {
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }

            .images {
                .woocommerce-product-gallery__trigger {
                    right: 10px;
                }
            }

            .mkdf-single-product-summary {
                margin: 30px 0 0;
            }
        }

        .woocommerce-tabs {
            ul.tabs {
                > li {
                    a {
                        @include mkdfH3();
                    }
                }
            }
        }
    }
}

@include ipad-portrait() {
    .mkdf-woo-single-page {
        .woocommerce-tabs {
            .woocommerce-Tabs-panel {
                padding: 30px 6% 0 5%;
            }

            ul.tabs {
                > li {
                    a {
                        padding: 12px 12px;
                    }
                }
            }
        }
    }
}

@include phone-landscape() {
    .mkdf-woo-single-page {
        &.mkdf-woo-single-thumb-on-left-side {
            .mkdf-single-product-content {
                .images {
                    &.woocommerce-product-gallery--columns-4 {
                        .woocommerce-product-gallery__image {
                            &:not(:first-child) {
                                width: 50%;
                            }
                        }
                    }
                }
            }
        }

        .mkdf-single-product-content {
            .images {
                &.woocommerce-product-gallery--columns-4 {
                    .woocommerce-product-gallery__image {
                        &:not(:first-child) {
                            width: 50%;
                        }
                    }
                }
            }
        }

        .woocommerce-tabs {
            ul.tabs {
                > li {
                    display: block;
                }
            }

            .woocommerce-Tabs-panel {
                padding: 30px 0 0 0;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */